/* Header */

.header {
    padding: 40px 0;
    min-height: 340px;

    display: flex;
    justify-content: center;
    align-items: center;

    background-color: var(--header-color);
    /* background-image: url("./../img/header-2png.png");
    background-repeat: no-repeat;
    background-size: 335px;
    background-position: center; */

    color: var(--text-color);
    text-align: center;
}

.header__wrapper {
    padding: 0 15px;
    max-width: 660px;
}

.header__title {
    font-size: 40px;
    font-weight: 700;
    line-height: 1.4;
}

.header__title strong {
    font-size: 60px;
    font-weight: 700;
}

.header__title em {
    color: var(--accent);
}

.header__text {
    margin-bottom: 40px;
    font-style: 18px;
    line-height: 1.333;
}

.header__text p + p {
    margin-top: 0.5em;
}