/* Projects */

.projects {
    display: flex;
    flex-wrap: wrap;
    column-gap: 30px;
    row-gap: 30px;
    justify-content: center;
}

.project {
    max-width: 370px;
    background-color: var(--header-color);
    box-shadow: var(--box-shadow);
    border-radius: 10px;
}

.project__img {
    border-radius: 10px;
}

.project__title {
    padding: 15px 20px 25px;
    font-weight: 700;
    font-size: 24px;
    line-height: 1.3;
    text-align: center;
    color: var(--accent);

    transition: opacity 0.2s ease-in;
}

.project:hover {
    opacity: 0.8;
}