/* Footer */

.footer {
    height: 125px;
    margin-top: 125px;
    padding: 20px 0;
    background-color: var(--header-color);
    color: var(--text-color);
}

.footer-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    row-gap: 27px;
}

.social {
    display: flex;
    column-gap: 30px;
    align-items: center;
}

.social__item {
    transition: opacity 0.3s ease-in;
}

.social__item:hover {
    opacity: 0.5;
}

.copyright {
    font-size: 16px;

}

.copyright p + p {
    margin-top: 0.5em;
}