@import "./reset.css";
@import url('https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;500;700&family=Poppins:wght@400;500;700&display=swap');

:root {
    --blue-border: #4d587a;
    --brown-border:#806354 ;
    --white: #ffffff;
    --deep-blue-color: #0F4C81;
    --navy-blue-color: #1A2C5B;
    --ligth-theme-green: #559e6a;
    --ligth-theme-peach: #95786a;
    --light-theme-peach2: #F8D6C4;
    --light-theme-purple: #625779;
    --light-theme-black: #292929;
    --header-text: var(--white);

    /* Light mode */
    --accent: var(--light-theme-purple);
    --header-color: var(--ligth-theme-peach);
    --header-border: var(--brown-border);
    --page-bg-start: var(--header-color);
    --page-bg-end: var(--light-theme-peach2);
    --text-color: var(--light-theme-black);
    --box-shadow: 0px 5px 35px rgba(0, 0, 0, 0.25)
}

.dark {
    /* Dark mode */
    --accent: var(--ligth-theme-green);
    --header-color: var(--navy-blue-color);
    --header-border: var(--deep-blue-color);
    --page-bg-start: var(--header-color);
    --page-bg-end: var(--blue-border);
    --text-color: var(--white);
    --box-shadow: 0px 5px 35px rgba(0, 0, 0, 0.8)
}

html, body {
    
    height: 100%;    

    font-family: 'DM Sans', sans-serif;
    letter-spacing: -0.5px;

    /* Color mode */
    background-image: linear-gradient(to bottom, var(--page-bg-start), var(--page-bg-end));
    color: var(--text-color);
}

#root {
    min-height: 100vh;
}

.App {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
}

/* General */

.container {
    margin: 0 auto;
    padding: 0 15px;
    max-width: 1200px;
}

.none {
    display: none !important;
}

.section {
    padding: 20px 0;
    width: auto;
    min-height: 100vh;
    background-image: linear-gradient(to bottom, var(--page-bg-start), var(--page-bg-end));
    color: var(--text-color);
    margin-bottom: -125px;
}

.title-1 {
    margin-bottom: 45px;
    font-size: 60px;
    font-weight: 700;
    line-height: 1.3;
    color: var(--accent);
    text-align: center;
}

.title-2 {
    margin-bottom: 20px;
    font-size: 40px;   
    font-weight: 700;
    line-height: 1.3;
}

/* Button */

.btn {
    display: inline-block;
    height: 48px;
    padding: 12px 28px;

    border-radius: 5px;
    background-color: var(--accent);
    color: var(--header-text);
    letter-spacing: 0.15px;
    font-size: 16px;
    font-weight: 500;

    transition: opacity 0.2s ease-in;
}

.btn:hover {
    opacity: 0.8;
}

.btn:active {
    position: relative;
    top: 1px;
}

/* Content-list */

.content-list {
    margin: 0 auto;
    max-width: 570px;
    display: flex;
    flex-direction: column;
    align-items: center;
    row-gap: 40px;
    text-align: center;
}

.content-list a {
    color: var(--accent);
}

.content-list__item {
    font-size: 18px;
    line-height: 1.5;
}

.content-list__item p + p {
    margin-top: 0.5em;
}

@media (max-width: 620px) {
    .header {
        min-height: unset;
    }

    .header__title {
        font-size: 30px;
    }

    .header__title strong {
        font-size: 40px;
    }

    
    .title-1 {
        margin-bottom: 30px;
        font-size: 40px;
    }

    .title-2 {
        margin-bottom: 10px;
        font-size: 30px;
    }

    .project__title {
        font-size: 22px;
    }

    .footer-wrapper {
        row-gap: 20px;
    }

    .social {
        column-gap: 20px;
    }

    .social__item {
        width: 28px;
    }

    .content-list {
        row-gap: 20px;
    }

    .content-list__item {
        font-size: 16px;
    }

}

@media (max-width: 510px) {
    .nav-row {
        justify-content: space-between;
    }

    .dark-mode-btn {
        order: 0;
    }
}